import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import Container from "@mui/material/Container";
import { useState } from "react";
import { editFruit } from "../functions";
import ImageUploading from "react-images-uploading";

export default function EditDialog(props) {
  const { data, onClose, open } = props;
  const [imageFile, setImageFile] = useState(null)
  const [fruit, setFruit] = useState({name:data.name, image:data.image})
  const [errorFruit, setErrorFruit] = useState(false)
  const [images, setImages] = useState([{"data_url":data.image}]);
  const maxNumber = 69;

  const imgStyle = {
    display: 'block',
    marginLeft: 'auto',
    marginRight: 'auto',
    marginTop: '20px'
  }

  const onChange = (imageList, addUpdateIndex) => {
    // data for submit
    console.log(imageList, addUpdateIndex);
    if(imageList[0]) setFruit({ ...fruit, image: imageList[0]["data_url"] });
    setImages(imageList);
  };

  const handleClose = () => { 
    onClose(false);
  };

  const handleChangeName = (e) => {
    setFruit({ ...fruit, name: e.target.value })
  }
  
  // const handleChangeImage = (e) => {
  //   getBase64(e.target.files[0])
  //   .then(result => {
     
  //     setFruit({...fruit, image: result})
  //   })
  //   .catch(err => {
  //     console.log(err);
  //   });
  // }

  // const getBase64 = file => {
  //   return new Promise(resolve => {
  //     let fileInfo;
  //     let baseURL = "";
  //     let reader = new FileReader();

  //     reader.readAsDataURL(file);

  //     reader.onload = () => {
  //       // Make a fileInfo Object
  //       console.log("Called", reader);
  //       baseURL = reader.result;
  //       console.log(baseURL);
  //       resolve(baseURL);
  //     };
  //     console.log(fileInfo);
  //   });
  // };

  const onSubmitEditHandler = async (e) => {
    e.preventDefault();
    setErrorFruit(false)
    if (fruit.name == ""){
      setErrorFruit(true)
    }else{
      const result = await editFruit(data._id, fruit);
      onClose(false)
      window.location.reload()
    }
  };

  return (
    <>
      <Dialog
        open={open}
        onClose={handleClose}
        PaperProps={{
          style: { width: "80%", height: "500px" },
        }}
      >
        <Container>
          <DialogContent>
            <br />
            <TextField
              autoFocus
              fullWidth
              id="name"
              label="Name"
              variant="outlined"
              value={fruit.name}
              onChange={handleChangeName}
              error={errorFruit}
              helperText={errorFruit && "Please enter fruit name"}
            />
            <ImageUploading
              value={images}
              onChange={onChange}
              maxNumber={maxNumber}
              dataURLKey="data_url"
            >
              {({
                imageList,
                onImageUpload,
                onImageRemoveAll,
                onImageUpdate,
                onImageRemove,
                isDragging,
                dragProps,
              }) => (
                // write your building UI
                <div className="upload__image-wrapper">
                  <Button
                    fullWidth
                    sx={{marginTop: '20px'}}
                    variant="contained"
                    style={isDragging ? { color: "red" } : undefined}
                    onClick={onImageUpload}
                    {...dragProps}
                  >
                    Click or Drop here
                  </Button>
                  {imageList.map((image, index) => (
                    <div key={index} className="image-item">
                      <img src={image["data_url"]} alt="" width="300" style={imgStyle} />
                     
                    </div>
                  ))}
                </div>
              )}
            </ImageUploading>
            
          </DialogContent>
         
          <Button
            variant="contained"
            onClick={onSubmitEditHandler}
            sx={{ marginTop: "20px" }}
            fullWidth
          >
            Update
          </Button>
        </Container>
      </Dialog>
    </>
  );
}
